<template>
  <v-container>
    <div class="submit-form mt-3 mx-auto">
      <v-row>
        <v-col class="text-center" cols="12" md="8">
          <v-row class="pa-5">
            <h2>ADD INSURER</h2>
          </v-row>
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/inss"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >INSURER LIST</v-btn
          >
        </v-col>
      </v-row>

      <div>
        <v-form ref="form" lazy-validation>
          <v-text-field
            v-model="insurer.name"
            :rules="[(v) => !!v || 'Name is required']"
            label="Name"
            required
          ></v-text-field>

          <v-text-field
            v-model="insurer.insCode"
            :rules="[(v) => !!v || 'Code is required']"
            label="Code"
            required
          ></v-text-field>
        </v-form>

        <v-btn
          :disabled="isAddButtonDisabled"
          color="primary"
          class="mt-3"
          @click="saveIns"
          >Submit</v-btn
        >
      </div>
    </div>
  </v-container>
</template>

<script>
import InsDataService from "../../services/InsDataService";

export default {
  name: "add-ins",
  data() {
    return {
      insurer: {
        id: null,
        name: "",
        insCode: "",
      },
    };
  },

  computed: {
    isAddButtonDisabled() {
      return !(this.insurer.name && this.insurer.insCode);
    },
  },

  methods: {
    saveIns() {
      var data = {
        name: this.insurer.name,
        insCode: this.insurer.insCode,
      };

      InsDataService.create(data)
        .then((response) => {
          this.insurer.id = response.data.id;
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "inss" });
        })
        .catch((e) => {
          console.log(e);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (e.response && e.response.data && e.response.data.message) ||
              e.message ||
              toString(),
          });
        });
    },
  },
  mounted() {},
};
</script>
